// import fetch from 'node-fetch'
import fetch from '@/utils/my-fetch';
import { API_HOST } from '@/config/constants';

// Part 1. ACTION TYPES
export const actionTypes = {
  GET_TAG_LIST: 'GET_TAG_LIST',
  GET_TAG_WITH_POSTS: 'GET_TAG_WITH_POSTS'
};

// Part 2 .ACTIONS
export const getTagList = () => async dispatch => {
  const url = `${API_HOST}/api/tags`
  const result = await fetch(url);

  return dispatch({
    type: actionTypes.GET_TAG_LIST, 
    data: result.code === 200 ? result.data : {} 
  })
};

export const getTagWithPosts = ({ id }) => async dispatch => {
  const url = `${API_HOST}/api/tags/${id}?status=published`;
  const result = await fetch(url);

  return dispatch({
    type: actionTypes.GET_TAG_WITH_POSTS, 
    data: result.code === 200 ? result.data : {} 
  })
};




